<template>
    <div style="height: auto; width: auto;">

        <!--  -->
        <el-card>
            <div style="display: flex; flex-direction: row;">
                <!--  -->
                <div style="flex: 8;">
                    <el-form :inline="true">
                        <el-form-item label="">
                            <el-input v-model="params.name" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="params.description" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="params.statue" placeholder=""></el-input>
                        </el-form-item>

                        <el-form-item label="">
                            <el-date-picker v-model="params.ctime" type="datetime" placeholder="">
                            </el-date-picker>
                        </el-form-item>

                        <el-form-item label="">
                            <el-select v-model="params.typeId" placeholder="" size="medium">
                                <el-option value=""></el-option>
                                <el-option :label="type.name" :value="type.id" :key="type.id"
                                    v-for="type in typeList"></el-option>
                            </el-select>
                        </el-form-item>


                        <el-form-item>
                            <el-button @click="search" type="primary" icon="el-icon-search"></el-button>
                        </el-form-item>

                    </el-form>
                </div>
            </div>
        </el-card>

        <!--  -->
        <el-card style="margin-top: 20px;">

            <el-card>
                <span
                    style="font-size: 16px;font-weight: bold;font-family: 'Times New Roman', Times, serif;"></span>
                <div style="border: 2px; margin-top: 20px;">
                    <el-button type="primary" @click="addopen"></el-button>
                </div>
            </el-card>

            <div>
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column prop="id" label="ID"></el-table-column>
                    <el-table-column prop="name" label=""></el-table-column>
                    <el-table-column prop="description" label=""></el-table-column>
                    <el-table-column prop="depreciationType.name" label=""></el-table-column>
                    <el-table-column prop="depreciationType.depreciationMethod.depreciationName"
                        label=""></el-table-column>
                    <el-table-column prop="originalValue" label=""></el-table-column>
                    <el-table-column prop="depreciationAmount" label=""></el-table-column>
                    <el-table-column prop="statue" label=""></el-table-column>
                    <el-table-column prop="ctime" label="">

                    </el-table-column>
                    <el-table-column label="" width="180">
                        <template slot-scope="scope">
                            <el-button type="text" @click="showDepreciationDetails(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>

                <el-dialog title="" :visible.sync="dialogVisible">
                    <el-table :data="depreciationDetails" style="width: 100%">
                        <el-table-column prop="id" label="ID"></el-table-column>
                        <el-table-column prop="depreciationAmount" label=""></el-table-column>
                        <el-table-column prop="duringTime" label="" width="180">
                            <template slot-scope="scope">
                                {{ new Date(scope.row.duringTime).toLocaleDateString() }}
                            </template>
                        </el-table-column>
                        <el-table-column fixed="right" label="" align="center">
                            <template #default="{ row }">
                                <el-button v-if="row.voucherState === 0" type="text" size="medium"
                                    icon="el-icon-document" @click="updateApprovalStatus(row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false"></el-button>
                    </span>
                </el-dialog>
            </div>

            <!--  -->
            <el-pagination @current-change="pagechange" background layout="prev, pager, next" :total="total"
                :page-size="pagesize" style="text-align: center; margin-top: 10px;">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="addshow" width="600px">
            <el-form :model="formData" class="demo-form-inline" label-width="80px">

                <el-form-item label="" label-width="120px" :rules="[
                        { required: true, message: '', trigger: 'blur' }
                    ]">
                    <el-input type="text" v-model="formData.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px" :rules="[
                        { required: true, message: '', trigger: 'blur' }
                    ]">
                    <el-input type="text" v-model="formData.statue" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <template>
                        <el-input-number v-model="formData.originalValue" :step="2" step-strictly></el-input-number>
                    </template>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <template>
                        <el-input-number v-model="formData.depreciationAmount" :step="2"
                            step-strictly></el-input-number>
                    </template>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-select v-model="formData.typeId" placeholder="" size="medium">
                        <el-option value=""></el-option>
                        <el-option :label="type.name" :value="type.id" :key="type.id"
                            v-for="type in typeList"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input type="textarea" v-model="formData.description" placeholder=""
                        :autosize="{ minRows: 4, maxRows: 6 }"></el-input>
                </el-form-item>

                <!--  Flex  -->
                <el-form-item class="button-group">
                    <el-button @click="addsubmit" type="primary"></el-button>
                    <el-button @click="cancel" type="info"></el-button>
                </el-form-item>

            </el-form>
        </el-dialog>

    </div>
</template>

<script>

import axios from "axios";
import dayjs from "dayjs";
import moment from 'moment';
import { Table, TableColumn, Button, Dialog } from 'element-ui'

export default {

    components: { Table, TableColumn, Button, Dialog },
    data() {
        return {
            tableData: [{ ctime: '' }],
            depreciationDetails: [],
            dialogVisible: false,
            params: {
                name: '',
                description: '',
                typeId: '',
                statue: '',
                ctime: ''
            },
            formData: {
                name: '',
                statue: '',
                originalValue: '',
                depreciationAmount: '',
                description: '',
                typeId: '',
                bookId: 4
            },
            page: 1,
            limit: 5,
            total: 0,
            addshow: false,
            typeList: [],
            methodList: []
        }
    },
    mounted() {
        this.fetchData()
        this.getType()
    },
    methods: {
        async fetchData() {
            const res = await axios({
                method: "get",
                url: "http://localhost:8081/depreciation/page",
                params: {
                    page: this.page,
                    limit: this.limit,
                    ...this.params
                },
            });
            this.tableData = res.data.data.map(item => ({
                ...item,
                ctime: moment(item.ctime).format('YYYY-MM-DD HH:mm:ss')
            }));

            this.total = res.data.count;
        },
        showDepreciationDetails(row) {
            this.depreciationDetails = row.depreciationDetailContentDepreciationDetailContentList
            this.dialogVisible = true
        },
        async getType() {
            const res = await axios({
                method: "get",
                url: "http://localhost:8081/depreciationType/findAll",
            });
            this.typeList = res.data.data
            this.typeList = this.typeList.filter(type => type.statue !== '');
            console.log(this.typeList)
        },
        // 
        search() {
            this.fetchData()
        },
        //
        formatDate(value) {
            return moment(value).format('YYYY-MM-DD HH:mm:ss');
        },
        async addsubmit() {
            const data = {
                ...this.formData
            }
            const res = await axios({
                url: "http://localhost:8081/depreciation/add",
                method: "post",
                data: data
            })
            this.fetchData()
            this.addshow = false
        },
        addopen() {
            this.addshow = true
        },
        async updateApprovalStatus(row) {
            console.log(row)
            const res = await axios({
                url: "http://localhost:8081/salary/updateStatus",
                method: "get",
                params: {
                    id: row.id
                }
            })
            // this.getList()
            localStorage.setItem('summary', this.summary)
            localStorage.setItem('debitAmount', row.zg)
            localStorage.setItem('text', this.text)
            // 
            Message.success('')
            setTimeout(() => {
                // 
                this.$router.push('/voucher/Voucher?type=1');
            }, 1000);
        }
    }
}
</script>

<style lang="scss" scoped>
.row-box {
    width: 100%;
    display: flex;
    margin-top: 20px;
    margin-bottom: 8px;
    justify-content: center;
}
</style>